.cart {
  padding-top: 60px;
  padding-bottom: 40px;

  &__container {
    overflow: hidden;
  }

  &__title {
    margin-bottom: 50px;
  }

  &__list {
    margin-right: 20px;
    width: 860px;
  }

  &__item {
    margin-bottom: 40px;
    padding: 30px;

    border: 1px solid var(--color-drivider);
    border-radius: 8px;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      cursor: pointer;
    }

    &-active {
      margin-bottom: 20px;
    }

    &-title {
      color: var(--color-gray);

      &--color {
        color: var(--color-main);
      }
    }
  }

  &__flex {
    display: flex;
  }

  &__right {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__result {
    margin-bottom: 20px;

    &-arrange {
      font-weight: 500;
      color: var(--color-white);

      padding: 16px 118px;

      background-color: var(--color-gray);
      border-radius: 4px;
    }
  }

  &__checkbox {
    display: flex;
    align-items: center;

    &-input {
      width: 20px;
      height: 20px;
      margin-right: 12px;
    }

    &-confirm {
      max-width: 326px;

      line-height: 130%;
      color: var(--color-main);
    }
  }

  &__btn {
    display: none;
    margin-bottom: 40px;
    padding: 14px 76px;

    font-weight: 700;
    color: var(--color-white);

    background-color: var(--color-blue);
    border-radius: 4px;
    transition: background-color .2s linear;

    &:hover {
      transition: background-color .2s linear;
      background-color: var(--color-darkblue);
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__next {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-btn {
      font-weight: 700;
      color: var(--color-main);

      padding: 14px 61px;

      border: 1px solid var(--color-drivider);
      border-radius: 4px;
      transition: color .2s ease-in-out, border .2s ease-in-out;

      &:hover {
        transition: color .2s ease-in-out, border .2s ease-in-out;
        color: var(--color-blue);
        border: 1px solid var(--color-blue);
      }
    }
  }
}

.product {

  &__list {
    margin-top: 30px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    padding-bottom: 30px;

    border-bottom: 1px solid var(--color-drivider);

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;

      border: none;
    }
  }

  &__info {
    display: flex;
    align-items: center;

    &-picture {
      margin-right: 20px;
    }

    &-model {
      line-height: 130%;
      color: var(--color-main);

      max-width: 280px;

      &:hover {
        color: var(--color-blue);
      }
    }
  }

  &__quantity {
    display: flex;
    align-items: center;
    
    &-minus {
      display: block;
      width: 12px;
      margin-right: 10px;

      border: 2px solid var(--color-drivider);

      &:hover {
        cursor: pointer;
      }
    }

    &-number {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      width: 48px;
      height: 48px;

      border: 1px solid var(--color-drivider);
      border-radius: 4px;
    }

    &-plus {
      display: block;
      width: 20px;
      height: 20px;
      margin-left: 10px;

      &:hover {
        cursor: pointer;
      }
    }
  }

  &__price {

    &-old {
      font-weight: 700;
      color: var(--color-gray);
      text-decoration: line-through;
    }

    &-new {
      font-weight: 700;
      font-size: 24px;
      color: var(--color-main);
    }
  }

  &__delete {

    &-btn {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      width: 48px;
      height: 48px;
    }
  }

  &__selected {
    display: none;
    margin-top: 30px;

    &-preview {
      display: flex;
      align-items: center;
    }

    &-img {
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }

    &--active {
      display: flex;
    }
  }
}

.address {

  &__inner {
    margin-top: 20px;
  }

  &__top {
    display: flex;
    margin-bottom: 30px;
    padding-bottom: 30px;

    border-bottom: 1px solid var(--color-drivider);
  }

  &__list {
    margin-right: 30px;

    &-flex {
      display: flex;
      align-items: center;
      justify-content: space-between;

      width: 310px;
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;
    }

    &-city {
      color: var(--color-main);
    }
  }

  &__method {
    display: flex;
    align-items: center;
    width: 310px;
    margin-bottom: 20px;
    padding: 16px 20px;

    border: 1px solid var(--color-card-border);
    border-radius: 5px;

    &-input {
      width: 20px;
      height: 20px;
      margin-right: 10px;
    }

    &-label {
      color: var(--color-blue);
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  &__form {
    display: flex;
    flex-direction: column;

    &-label {
      margin-bottom: 8px;

      color: var(--color-main);
    }

    &-list {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;
    }

    &-text {
      color: var(--color-main);

      span {
        color: var(--color-gray);
      }
    }

    &-input {
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;
    }

    &-comment {
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;

      resize: none;
    }

    &-textarea {
      grid-column: 2 span;
    }
  }

}

.pickup {
  display: none;
  justify-content: space-between;

  &__availabel {
    line-height: 100%;
    color: var(--color-main);

    margin-bottom: 30px;
  }

  &__list {

    &-item {
      display: flex;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__radio {
    width: 20px;
    height: 20px;
    margin-right: 13px;
  }

  &__place {
    display: flex;
    flex-direction: column;
    
    &-address {
      max-width: 400px;
      
      font-size: 20px;
      line-height: 120%;
      color: var(--color-main);

      margin-bottom: 10px;
    }

    &-worktime {
      line-height: 100%;
      color: var(--color-gray);
    }
  }
}

.peyment {
  
  &__inner {
    margin-top: 30px;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 390px;
    padding: 16px 20px;

    border: 1px solid var(--color-card-border);
    border-radius: 5px;
  }
}

.recipient {
  
  &__form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin: 20px 0;

    &-field {
      display: flex;
      flex-direction: column;
    }

    &-label {
      color: var(--color-main);
      margin-bottom: 8px;
    }

    &-input {
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;
    }
  }

  &__checkbox {
    display: flex;
    align-items: center;

    &-input {
      width: 20px;
      height: 20px;
      margin-right: 7px;
    }
  }
}

// cart right

.result {
  width: 420px;
  max-height: 354px;
  padding: 30px;

  background-color: var(--color-light-blue);
  border-radius: 8px;

  &__title {
    margin-bottom: 17px;
  }

  &__top {
    margin-bottom: 30px;

    border-bottom: 1px solid var(--color-drivider);
  }
  
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 30px;
    }

    &-descr {
      font-size: 20px;
      line-height: 120%;
      color: var(--color-gray);
    }

    &-meaning {
      font-size: 20px;
      line-height: 120%;
      color: var(--color-main);
    }
  }

  &__peyment {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;

    &-title {
      margin-bottom: 0;
    }

    &-price {
      font-weight: 700;
      font-size: 36px;
      line-height: 130%;
      color: var(--color-main);
    }
  }
}


// media

@media(max-width: 1200px) {
  .cart {

    &__flex {
      flex-direction: column;
      align-items: flex-start;
    }

    &__list {
      width: 100%;
    }

    &__right {
      width: 100%;
      align-items: flex-start;
    }

    &__result {
      width: 100%;

      &-arrange {
        width: 100%;
      }
    }

    &__checkbox {

      &-confirm {
        max-width: none;
      }
    }
  }
}

@media(max-width: 768px) {
  .cart {
    padding-top: 30px;

    &__title {
      margin-bottom: 30px;
    }
    
    &__checkbox {
      align-items: flex-start;

      &-confirm {
        max-width: 360px;
      }
    }
  }

  .product {

    &__item {
      flex-direction: column;
      align-items: flex-start;
      padding: 20px;
    }

    &__info {
      margin-bottom: 20px;
    }

    &__quantity {
      margin-bottom: 15px;
    }

    &__delete {
      align-self: flex-end;
    }
  }

  .address {

    &__top {
      flex-direction: column;
    }

    &__list {
      margin-right: 0;
      margin-bottom: 20px;
    }

    &__grid {
      grid-template-columns: repeat(1, 1fr);
    }

    &__form {

      &-textarea {
        grid-column: 1 span;
      }
    }
  }

  .recipient {
    
    &__form {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  .result {

    &__info {

      &-descr {
        font-size: 16px;
      }

      &-meaning {
        font-size: 16px;
      }
    }

    &__peyment {

      &-price {
        font-size: 24px;
      }
    }
  }
}

@media(max-width: 520px) {
  .cart {

    &__item {
      padding: 15px;
    }

    &__result {

      &-arrange {
        padding: 16px 60px;
      }
    }

    &__checkbox {

      &-confirm {
        max-width: 250px;
      }
    }
  }

  .product {

    &__price {

      &-new {
        font-size: 20px;
      }
    }
  }

  .peyment {

    &__list {
      width: 100%;
      max-width: 330px;
    }
  }

  .recipient {

    &__checkbox {

      &-descr {
        max-width: 251px;
      }
    }
  }
}

@media(max-width: 375px) {
  .cart {

    &__result {
      padding: 20px;

      &-arrange {
        width: 245px;
      }
    }
  }

  .address {

    &__list {
      
      &-flex {
        width: 260px;
      }
    }

    &__method {
      width: 260px;
    }
  }

  .recipient {

    &__checkbox {

      &-descr {
        max-width: 201px;
      }
    }
  }
}


