.promo {
  padding-top: 40px;
  padding-bottom: 80px;

  &__content {
    display: flex;
  }

  &__article {
    margin-right: 20px;
  }

  &__text {
    color: var(--color-main);

    margin-bottom: 40px;
    max-width: 750px;
  }

  &__list {
    margin-left: 15px;
    max-width: 750px;
  }

  &__item {
    padding-bottom: 15px;
    list-style-type: decimal;

    &:last-child {
      padding-bottom: 0;
    }
  }

  &__right {

    &-picture {

      &--media {
        display: none;
      }
    }
  }
}

// media

@media(max-width: 998px) {
  .promo {

    &__content {
      flex-direction: column;
    }

    &__article {
      margin-right: 0;
      margin-bottom: 30px;
    }

    &__right {

      &-picture {
        display: none;

        &--media {
          display: block;
        }
      }
    }
  }
}

@media(max-width: 768px) {
  .promo {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}