.product-descr {
  padding-bottom: 80px;

  &__tabs {
    display: flex;
    align-items: center;
    width: 100%;
    height: 72px;
    margin-bottom: 40px;
    padding: 0 20px;

    background-color: var(--color-light-blue);
    border-radius: 8px;

    &-btn {
      height: 72px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-weight: 700;
      color: var(--color-main);

      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        color: var(--color-darkblue);
        border-bottom: 1px solid var(--color-darkblue);
      }
    }

    &-text {
      line-height: 120%;
      color: var(--color-main);

      padding-bottom: 10px;

      &:last-child {
        padding-bottom: 0;
      }
    }
  }

  &__text {

    &-title {
      margin-bottom: 30px;
    }
  }

  &__specif {
    display: none;
  }

  &__table {
    max-width: 975px;
    text-align: left;

    tr {
      border-bottom: 1px solid var(--color-card-border);
    }

    th {
      width: 487px;
      padding: 26px 0;
      padding-left: 20px;

      font-weight: 700;
      color: var(--color-main);
    }

    td {
      width: 487px;
      padding: 26px 0;

      color: var(--color-main);
    }
  }

  &__reviews {
    display: none;

    &-title {
      font-weight: 400;
      margin-bottom: 30px;
    }

    &-content {
      display: flex;
    }

    &-left {
      margin-right: 130px;
      padding: 40px;
      max-width: 640px;

      background-color: var(--color-light-blue);
      border-radius: 10px;
    }

    &-info {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }

    &-profile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 50px;
      margin-right: 20px;

      font-weight: 700;
      font-size: 20px;
      color: var(--color-main);

      background-color: var(--color-drivider);
      border-radius: 100%;
    }

    &-name {
      font-weight: 700;
      color: var(--color-main);

      margin-right: 6px;
    }

    &-time {
      font-weight: 700;
      color: var(--color-gray);

      margin-right: 17px;
    }

    &-rating {
      display: flex;
      align-items: center;

      &-icon {
        margin-right: 12px;
      }

      span {
        font-weight: 700;
        color: var(--color-gray);
      }
    }

    &-subtitle {
      font-weight: 700;
      line-height: 130%;
      color: var(--color-main);

      margin-bottom: 10px;
    }

    &-comment {
      line-height: 130%;
      color: var(--color-main);
    }
  }

  &__letter {

    &-title {
      font-weight: 700;
      color: var(--color-main);

      margin-bottom: 12px;
    }

    &-text {
      color: var(--color-main);
      margin-bottom: 20px;
    }

    &-btn {
      width: 310px;
      padding: 14px 95px;

      color: var(--color-white);

      background-color: var(--color-blue);
      border-radius: 4px;
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-darkblue);
      }
    }
  }
}

.tabs-item {
  display: none;
}

.tabs-active {
  display: block;
}

// media

@media(max-width: 1024px) {
  .product-descr {

    &__reviews {

      &-content {
        flex-direction: column;
      }

      &-left {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }
} 

@media(max-width: 768px) {
  .product-descr {
    padding-bottom: 50px;

    &__reviews {

      &-info {
        flex-wrap: wrap;
      }

      &-time {
        margin-right: 0;
      }

      &-rating {
        margin-top: 20px;
      }
    }
  }
}

@media(max-width: 520px) {
  .product-descr {

    &__tabs {
      overflow: auto;

      &-btn {
        font-size: 15px;
      }
    }

    &__table {

      tr {
        display: flex;
        flex-direction: column;
      }

      th {
        width: 360px;
        padding-left: 0;
        padding: 10px 0;
      }

      td {
        width: 360px;
        padding: 15px 0;
      }
    }

    &__reviews {

      &-left {
        padding: 20px;
      }
    }
  }
}

@media(max-width: 375px) {
  .product-descr {

    &__table {

      th {
        width: 280px;
      }

      td {
        width: 280px;
      }
    }

    &__reviews {

      &-profile {
        margin-bottom: 10px;
      }

      &-rating {
        margin-top: 10px;
      }

    }

    &__letter {

      &-btn {
        width: 280px;
        padding: 14px 50px;
      }
    }
  }
}