.installment {
  padding-top: 40px;
  padding-bottom: 80px;

  &__img {
    margin-bottom: 30px;
  }

  &__article {
    margin-bottom: 30px;
  }

  &__explanation {

    &-text {
      color: var(--color-main);
      line-height: 130%;

      max-width: 975px;
      padding-bottom: 15px;

      &:last-child {
        padding-bottom: 0;
        margin-bottom: 15px;
      }
    }

    &-important {
      line-height: 130%;
      color: var(--color-main);

      max-width: 680px;
      margin-bottom: 30px;
      background-color: yellow;
    }

    &-img {
      margin-bottom: 20px;
    }

    &-descr {
      font-size: 14px;
      line-height: 130%;
      color: var(--color-main);

      margin-bottom: 10px;
      max-width: 975px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__arrange {
    display: flex;
    align-items: center;

    &-form {
      display: flex;
      flex-direction: column;
      margin-right: 57px;
      padding: 30px;

      background-color: var(--color-light-blue);
      border-radius: 8px;
    }

    &-label {
      color: var(--color-main);
      margin-bottom: 8px;
    }

    &-input {
      width: 100%;
      height: 48px;
      margin-bottom: 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;
    }

    &-submit {
      width: 100%;
      padding: 16px 114px;

      font-weight: 500;
      color: var(--color-white);
      
      background-color: var(--color-darkblue);
      border-radius: 4px;
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-blue);
      }
    }
  }

  &__bank {
    
    &-title {
      max-width: 433px;
    }

    &-text {
      color: var(--color-main);
      margin-bottom: 15px;
    }
  }

  &__options {
    max-width: 410px;
    margin-left: 30px;

    &-text {
      line-height: 130%;
      color: var(--color-main);

      list-style-type: disc;
    }
  }
}

// media

@media(max-width: 768px) {
  .installment {
    padding-top: 30px;
    padding-bottom: 50px;

    &__arrange {
      flex-direction: column;
      align-items: flex-start;

      &-form {
        margin-right: 0;
        margin-bottom: 30px;
        padding: 30px 20px;
      }
    }
  }
}

@media(max-width: 520px) {
  .installment {

    &__arrange {

      &-form {
        width: 280px;
      }

      &-submit {
        padding: 14px 80px;
      }
    }
  }
}