.catalog {
  padding-top: 40px;
  padding-bottom: 80px;

  &__product {
    display: flex;

    &-card {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__left {
    margin-right: 15px;
  }

  &__price {

    &-field {
      width: 117px;
      padding: 13px 20px;

      border: 1px solid #EAEAF0;
      border-radius: 4px;

      &::placeholder {
        color: var(--color-gray);
      }

      &--first {
        margin-right: 20px;
      }
    }

    &-slider {
      width: 100%;
      height: 6px;
      margin-bottom: 27px;

      background-color: var(--color-card-border);
      border-radius: 12px;
    }
  }

  &__inner {
    display: none;

    &-btn {
      width: 100%;
      padding: 16px 112px;

      font-weight: 500;
      color: var(--color-white);

      background-color: var(--color-blue);
      border-radius: 4px;
      transition: background-color 0.2s linear;

      &:hover {
        transition: background-color 0.2s linear;
        background-color: var(--color-darkblue);
      }
    }
    
    &-checkbox {
      display: flex;
      align-items: center;
      margin-bottom: 12px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__right {

    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;

      margin-bottom: 20px;
    }
  }

  &__more {
    grid-column: 3 span;
    margin-bottom: 40px;
    padding: 27px 405px;

    border: 1px solid var(--color-card-border);
    border-radius: 0px 0px 8px 8px;
    transition: background-color 0.2s linear;

    &-btn {
      display: flex;
      align-items: center;
      
      span {
        color: var(--color-main);
        margin-right: 10px;
      }
    }

    &:hover {
      transition: background-color 0.2s linear;
      background-color: var(--color-blue);
    }
  }
}

.filter {

  &__list {
    width: 310px;
    margin-bottom: 20px;
    overflow: hidden;

    &-text {
      font-weight: 700;
      line-height: 130%;

      color: var(--color-main);
    }

    &-main {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 11px 10px 20px;

      background-color: var(--color-light-blue);
      border-radius: 4px;
    }
  }

  &__inner {
    margin-top: 20px;

    background-color: var(--color-white);

    &-input {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }

    &-descr {
      color: var(--color-gray);
      margin-right: 10px;

      &--main {
        color: var(--color-main);
      }
    }
  }

  &__checkbox {
    width: 20px;
    height: 20px;
    margin-right: 12px;

    &-text {
      margin-right: 5px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__selected {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &-content {
      display: flex;
      align-items: center;
      margin-right: 20px;
      margin-bottom: 10px;
      padding: 6px 10px;

      background-color: var(--color-light-blue);
      border-radius: 102px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-text {
      font-size: 14px;
      color: var(--color-main);

      margin-right: 5px;
    }

    &-del {
      width: 10px;
      height: 14px;

      display: flex;
      align-items: center;
      justify-content: center;
    }

    &-clear {
      font-size: 14px;
      color: var(--color-red);

      margin-bottom: 10px;

      &:hover {
        border-bottom: 1px solid var(--color-red);
      }
    }
  }

  &__mobile {
    display: none;

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 12px 32px;
      background-color: var(--color-blue);
      border-radius: 4px;

      font-weight: 500;
      color: var(--color-white);

      &:hover {
        background-color: var(--color-darkblue);
      }
    }

    &-ico {
      margin-right: 10px;
    }
  }
}

.block {
  display: block;
}

// media

@media(max-width: 1350px) {
  .catalog {

    &__product {

      &-card {
        grid-template-columns: repeat(2, 1fr);
      }
    }

    &__more {
      grid-column: 2 span;
      padding: 27px 258px;
    }

    &__right {
      width: 100%;
    }
  }

  .filter {

    &__selected {
      margin-right: 30px;
    }
  }
}

@media(max-width: 1200px) {
  .filter {

    &__list {
      width: 260px;
    }
  }

  .catalog {

    &__price {

      &-field {
        max-width: 97px;
        padding: 13px 10px;

        &--first {
          margin-right: 10px;
        }
      }
    }
  }
}

@media(max-width: 998px) {
  .filter {
    display: none;

    &__selected {
      display: none;
    }

    &__mobile {
      display: block;
    }
  }
}

@media(max-width: 768px) {
  .catalog {
    padding-top: 30px;
    padding-bottom: 50px;

    &__product {

      &-card {
        grid-template-columns: repeat(1, 1fr);
      }
    }

    &__more {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      grid-column: 1 span;
      padding: 27px 56px;
    }
  }
}

@media(max-width: 520px) {
  .catalog {

    &__right-top {
      flex-direction: column;
    }
  }

  .filter {

    &__mobile {
      width: 240px;
      margin-bottom: 20px;

      &-btn {
        width: 100%;
      }
    }
  }
}