.news-section {
  padding-top: 40px;
  padding-bottom: 80px;

  &__content {
    margin-bottom: 40px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: auto;
    gap: 20px;
  }

  &__item {
    max-width: 640px;
    min-height: 269px;
    padding: 30px;

    background-color: var(--color-light-blue);
    border-radius: 8px;
  }

  &__card {
    display: flex;
    align-items: center;

    &-img {
      margin-right: 20px;
    }

    &-title {
      font-weight: 700;
      font-size: 28px;
      line-height: 120%;
      color: var(--color-main);

      margin-bottom: 20px;
    }

    &-text {
      line-height: 120%;
      color: var(--color-main);

      max-width: 410px;
      margin-bottom: 30px;
    }
  }
}

// media

@media(max-width: 1270px) {
  .news-section {

    &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    &__grid {
      grid-template-columns: repeat(1, 1fr);
    }

    &__item {
      max-width: 750px;
    }

    &__card {

      &-text {
        max-width: 498px;
      }
    }
  }
}

@media(max-width: 768px) {
  .news-section {
    padding-top: 30px;
    padding-bottom: 50px;

    &__card {

      &-title {
        font-size: 24px;
      }
    }
  }
}

@media(max-width: 520px) {
  .news-section {

    &__card {
      flex-direction: column;

      &-img {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }
}