.favorites {
  padding-top: 40px;
  padding-bottom: 80px;

  &__main {
    display: flex;
  }

  &__sign {
    max-width: 305px;
    height: 170px;
    margin-right: 20px;
    padding: 30px;

    border: 1px solid var(--color-card-border);
    border-radius: 8px;

    &-title {
      font-weight: 700;
      line-height: 130%;
      color: var(--color-main);

      max-width: 187px;
      margin-bottom: 20px;
    }

    &-btn {
      font-weight: 500;
      color: var(--color-white);

      padding: 16px 26px;
      background-color: var(--color-darkblue);
      border-radius: 4px;
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-blue);
      }
    }
  }

  &__menu {
    min-width: 175px;
    width: 305px;
    height: 324px;
    margin-right: 20px;

    &-list {
      border: 1px solid var(--color-card-border);
      border-radius: 8px;
    }

    &-item {
      font-weight: 500;
      line-height: 19px;
      color: var(--color-main);

      padding: 18px 20px;

      transition: background-color .2s linear;

      &:hover {
        cursor: pointer;
        transition: background-color .2s linear;
        background-color: var(--color-light-blue);
      }

      &:last-child {
        border-top: 1px solid var(--color-card-border);
        transition: color .2s ease-in-out;

        &:hover {
          transition: color .2s ease-in-out;
          color: var(--color-red);
        }
      }
    }
  }

  &__products {
    display: none;

    &-grid {
      grid-template-columns: repeat(3, 1fr);
    }

    &-item {
      border-radius: 0px 8px 8px 0px;
    }
  }

  &__profile {
    display: none;
    margin-left: 115px;

    &-subtitle {
      margin-bottom: 30px;
    }

    &-text {
      font-size: 17px;
      line-height: 21px;
      color: var(--color-main);
    }

    &-quantity {
      margin-left: 10px;
    }

    &-info {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }

    &-avatar {
      margin-right: 30px;
    }

    &-date {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }

    &-order {
      display: flex;
      align-items: center;
    }

    &-change {
      line-height: 130%;
      color: var(--color-main);

      max-width: 545px;

      a {
        font-weight: 600;
        color: var(--color-darkblue);
      }
    }

    &-control {
      margin-top: 20px;
      padding: 16px 80px;

      font-weight: 500;
      color: var(--color-white);

      background-color: var(--color-darkblue);
      border-radius: 5px;
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-blue);
      }
    }
  }
}

.personal-data {
  display: none;
  grid-template-columns: repeat(2, 310px);
  gap: 20px;
  margin-left: 115px;

  &__input {
    display: flex;
    flex-direction: column;

    &-label {
      color: var(--color-main);
      margin-bottom: 8px;
    }

    &-field {
      width: 310px;
      height: 58px;
      padding: 16px 20px;

      background-color: transparent;
      border: 1px solid var(--color-card-border);
      border-radius: 5px;

      &::placeholder {
        color: var(--color-gray);
      }
    }

    &-list {
      position: relative;
      display: flex;
      align-items: center;
    }

    &-btn {
      position: absolute;
      right: 20px;
    }
  }

  &__save {
    margin-top: 26px;
    padding: 16px 114px;

    font-weight: 500;
    color: var(--color-white);

    background-color: var(--color-darkblue);
    border-radius: 5px;
    transition: background-color .2s linear;

    &:hover {
      transition: background-color .2s linear;
      background-color: var(--color-blue);
    }
  }
}

.history {
  display: none;
  max-width: 975px;
  padding: 50px;

  background-color: #fbfdff;
  border: 1px solid var(--color-card-border);
  border-radius: 8px;

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__list {
    width: 875px;
    margin-bottom: 30px;
    padding-top: 20px;
    padding-bottom: 20px;

    border-top: 1px solid var(--color-drivider);
    border-bottom: 1px solid var(--color-drivider);

    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      padding-bottom: 20px;

      border-bottom: 1px solid var(--color-drivider);

      &:last-child {
        border: none;
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }

    &-number {
      color: var(--color-main);

      width: 190px;
      margin-right: 80px;

      span {
        color: var(--color-gray);
      }
    }

    &-info {
      width: 214px;
      margin-right: 163px;
      color: var(--color-main);
    }

    &-condition {
      width: 84px;
      margin-right: 153px;

      color: #22a44e;

      &--progress {
        color: var(--color-yellow);
      }
    }
  }
}

.pass {
  display: none;
  margin-left: 115px;

  &__input {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }

  &__label {
    color: var(--color-main);
    margin-bottom: 8px;
  }

  &__field {
    width: 310px;
    height: 48px;
    padding: 16px 20px;

    border: 1px solid var(--color-card-border);
    border-radius: 5px;
    transition: border .2s linear;

    &::placeholder {
      color: var(--color-gray);
    }

    &:focus {
      transition: border .2s linear;
      border: 1px solid #22A44E;
    }
  }

  &__save {
    width: 310px;
    padding: 16px 21px;

    font-weight: 500;
    color: var(--color-white);

    background-color: var(--color-darkblue);
    border-radius: 4px;
    transition: background-color .2s linear;

    &:hover {
      transition: background-color .2s linear;
      background-color: var(--color-blue);
    }
  }
}

// js

.no-active {
  display: none;
}

.personal-data--active {
  display: grid;
}

.active {
  display: block;
}

// media

@media(max-width: 1350px) {
  .favorites {

    &__products {

      &-grid {
        gap: 0;
        row-gap: 20px;
      }
    }
  }
}

@media(max-width: 1200px) {
  .favorites {

    &__products {
      width: 100%;

      &-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
      }
    }
  }

  .history {
    padding: 25px;

    &__list {
      width: 685px;

      &-item {
        justify-content: space-between;
      }

      &-number {
        margin-right: 20px;
      }

      &-info {
        margin-right: 40px;
      }

      &-condition {
        margin-right: 60px;
      }
    }
  }
}

@media(max-width: 998px) {
  .favorites {

    &__menu {
      display: none;
    }

    &__profile {
      margin-left: 0;

      &-change {
        max-width: 728px;
      }
    }
  }

  .pass {
    margin-left: 0;
  }

  .history {
    width: 100%;

    &__list {
      width: 100%;
    }
  }
}

@media(max-width: 768px) {
  .favorites {
    padding-top: 30px;
    padding-bottom: 50px;

    &__products {

      &-options {
        flex-direction: row;
      }

      &-grid {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }

  .history {

    &__list {

      &-number {
        display: flex;
        flex-direction: column;
        width: 120px;

        span {
          margin-top: 5px;
        }
      }
    }
  }
}

@media(max-width: 620px) {
  .history {

    &__list {

      &-item {
        flex-direction: column;
        align-items: flex-start;
        padding-bottom: 15px;
      }

      &-number {
        margin-bottom: 10px;
        margin-right: 0;
      }

      &-info {
        margin-bottom: 10px;
        margin-right: 0;
      }

      &-condition {
        margin-right: 0;
      }

      &-btn {
        align-self: flex-end;
      }
    }
  }
}

@media(max-width: 520px) {
  .favorites {

    &__products {

      &-options {
        flex-direction: column;
        align-items: center;
      }
    }

    &__profile {

      &-info {
        flex-direction: column;
        align-items: flex-start;
      }

      &-subtitle {
        margin-bottom: 20px;
      }

      &-avatar {
        margin-right: 0;
        margin-bottom: 10px;
      }
    }
  }
}

@media(max-width: 375px) {
  .favorites {

    &__profile {

      &-control {
        width: 280px;
        padding: 16px 65px;
      }
    }
  }

  .pass {

    &__field {
      width: 280px;
    }

    &__save {
      width: 280px;
    }
  }
}