.about {
  padding-top: 40px;
  padding-bottom: 80px;

  &__content {

    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
      color: var(--color-main);

      margin-bottom: 20px;
    }
  }

  &__text {
    line-height: 21px;
    color: var(--color-main);

    max-width: 975px;
    padding-bottom: 40px;
    
    span {
      font-weight: 700;
    }

    &-tel {

      &:hover {
        text-decoration: underline;
      }
    }

    &--smallpb {
      padding-bottom: 10px;
    }

    &:last-child {
      padding-bottom: 0;
      padding-bottom: 30px;
    }
  }

  &__address {
    max-width: 975px;
    margin-bottom: 40px;

    &-text {
      line-height: 21px;
      color: var(--color-main);

      padding-bottom: 10px;
    }

    &-link {
      font-weight: 700;
      color: var(--color-main);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__choice {
    margin-bottom: 40px;
    padding: 40px 0;
    background-color: var(--color-light-blue);

    &-title {
      margin-bottom: 30px;
    }

    &-product {
      margin-bottom: 40px;
    }

    &-list {
      display: grid;
      grid-template-columns: repeat(3, auto);
      column-gap: 20px;
      row-gap: 30px;
    }

    &-item {
      display: flex;
      align-items: center;
    }

    &-pic {
      width: 48px;
      height: 48px;
      margin-right: 10px;
    }

    &-link {
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: var(--color-main);

      &:hover {
        color: var(--color-blue);
      }
    }
  }
}

// media

@media(max-width: 1024px) {
  .about {

    &__choice {

      &-list {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }
}

@media(max-width: 768px) {
  .about {
    padding-top: 30px;
    padding-bottom: 50px;

    &__content {

      &-title {
        font-size: 18px;
      }
    }

    &__choice {

      &-list {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
}