.compare {
  padding-top: 40px;
  padding-bottom: 30px;

  &__title {
    margin-bottom: 78px;
  }

  &__product {
    display: flex;
    margin-bottom: 30px;

    &-right {
      margin-right: 40px;
    }

    &-list {
      margin-bottom: 20px;
    }
  }

  &__checkbox {
    display: flex;
    align-items: center;

    &-field {
      width: 20px;
      height: 20px;
      margin-right: 12px;

      border: 2px solid var(--color-card-border);
    }

    &-label {
      font-weight: 500;
      line-height: 130%;
      color: var(--color-main);
    }
  }

  &__card {

    &-delete {     
      position: absolute;
      left: 50%;
      top: -10px;

      color: var(--color-red);
      transform: translateX(-50%);
    }

    &-none {
      display: none;
    }

    &-top {
      height: 160px;
    }

    &-cart {
      width: 86px;
    }
  }

  &__article {
    width: 283px;
  }

  &__swiper {
    margin-top: -75px;

    &-top {
      display: flex;
      align-items: center;
      justify-content: flex-end;

      padding-top: 30px;
      margin-bottom: 20px;
    }

    &-prev {
      margin-right: 20px;

      background-image: url('../img/arrow-left.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: 38px 11px;
    }

    &-next {
      background-image: url('../img/arrow-right-dark.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: 38px 11px;
    }
  }

  &__table {
    max-width: 1300px;
    margin-bottom: 30px;

    &-grid {

      tr {
        display: grid;
        grid-template-columns: repeat(4, 320px);
      }

      th {
        text-align: left;
        padding: 26px 20px;
      }

      td {
        text-align: left;
        padding: 26px 20px;
      }
    }

    &-lightblue {
      background-color: var(--color-light-blue);
    }

    &-lightyellow {
      background-color: var(--color-light-yellow);
    }

    &-2 {
      display: none;
    }
  }
}

.swiper {
  width: 975px;
  height: 580px;
}

.swiper .swiper-slide {
  width: 325px;
}

.swiper-slide--media {
  display: none;
}

.swiper-button-prev,
.swiper-button-next {
  position: static;
  width: 48px;
  height: 48px;

  border: 1px solid var(--color-card-border);
  border-radius: 100%;

  &::after {
    display: none;
  }
}

// js

.table-none {
  display: none;
}

.table-active {
  display: block;
}

// media

@media(max-width: 1300px) {
  .compare {

    &__product {
      flex-direction: column;

      &-right {
        margin-right: 0;
        margin-bottom: 40px;
      }
    }

    &__swiper {

      &-prev {
        margin-right: 0;
      }
    }

    &__table {
      max-width: 1200px;

      &-grid {

        tr {
          grid-template-columns: repeat(3, 280px);
        }
      }
    }
  }
}

@media(max-width: 998px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }

  .compare {

    &__card {

      &-top {
        max-width: 230px;
      }
    }

    &__table {

      &-grid {

        tr {
          grid-template-columns: repeat(3, 200px);
        }
      }
    }
  }
}

@media(max-width: 768px) {
  .compare {
    padding-top: 30px;
    padding-bottom: 50px;

    &__table {

      &-grid {

        tr {
          grid-template-columns: repeat(2, 1fr);
        }
      }
    }
  }
}

@media(max-width: 520px) {
  .compare {
    
    &__table {

      &-grid {

        th {
          padding: 20px 10px;  
        }

        td {
          padding: 20px 10px;
        }
      }
    }
  }
}