.card {

    &__top {
        display: flex;
        align-items: flex-end;
        margin-bottom: 40px;

        &-link {
            font-weight: 500;
            color: var(--color-main);
            padding-right: 5px;

            &:hover {
                color: var(--color-darkblue);
            }
        }
    }

    &__wrapper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-rows: auto;
        row-gap: 20px;
    }

    &__item {
        width: 326px;
        max-height: 596px;
        padding: 20px;
        border: 1px solid #EAEAF0;
        transition: transform .1s ease-in-out;

        &-top {
            position: relative;
            width: 286px;
            height: 224px;
            margin-bottom: 10px;
        }

        &--first {
            border-radius: 8px 0px 0px 8px;
        }

        &--last {
            border-radius: 0px 8px 8px 0px;
        }

        &:hover {
            border: none;
            transition: transform .7s ease-in-out;
            transform: scale(1.05);
        }
    }

    &__link {
        position: absolute;
        right: -10px;
        top: 31px;
        font-weight: 500;
        color: var(--color-light-blue);

        padding: 5px 16px;
        border-radius: 4px;

        &-new {
            background-color: #48a69b;
        }

        &-hits {
            background-color: #d73838;
        }

        &--top {
            top: -10px;
        }
    }

    &__type {
        font-weight: 500;
        color: var(--color-gray);
        margin-bottom: 10px;
    }

    &__model {
        margin-bottom: 20px;

        a {
            font-size: 20px;
            color: var(--color-main);
    
            max-width: 286px;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__evaluation {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
    }

    &__rating {
        margin-right: 10px;
    }

    &__comment {

        &-img {
            margin-right: 5px;
        }
        
        &-quantity {
            font-weight: 500;
            color: var(--color-main);
        }
    }

    &__bottom {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        margin-bottom: 20px;

        &-icons {
            display: flex;
            align-items: center;
        }

        &-icon {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            width: 48px;
            height: 48px;
            margin-right: 10px;

            border: 1px solid #c8cacb;
            border-radius: 4px;

            &:last-child {
                margin-right: 0;
            }

            &:hover {
                border: 1px solid var(--color-blue);
            }
        }
    }

    &__btn {
        display: flex;
        align-items: center;

        &-buy {
            font-weight: 700;
            color: var(--color-blue);

            width: 228px;
            margin-right: 10px;
            padding: 14px 50px;
            border: 1px solid var(--color-blue);
            border-radius: 4px;
            transition: background-color .3s linear, color .3s linear;

            &:hover {
                transition: background-color .3s linear, color .3s linear;
                color: var(--color-white);
                background-color: var(--color-darkblue);
            }
        }

        &-cart {
            width: 48px;
            height: 48px;

            background-color: var(--color-blue);
            border-radius: 4px;
            transition: background-color .2s linear;

            &:hover {
                transition: background-color .2s linear;
                background-color: var(--color-darkblue);
            }
        }
    }
}

.price {

    &__old {

        &-text {
            font-weight: 700;
            color: var(--color-gray);
            text-decoration: line-through;
        }
    }

    &__new {
        margin-bottom: 5px;

        &-text {
            font-weight: 700;
            font-size: 24px;
            color: var(--corlor-main);
        }
    }

    &__discount {
        display: flex;
        align-items: center;
        width: 125px;
        padding: 3px 8px 3px 5px;

        background-color: #f37748;
        border-radius: 14px;

        &-persent {
            font-weight: 400;
            font-size: 14px;
            color: var(--color-main);

            padding: 0 4px;
            background-color: var(--color-white);
            border-radius: 23px;
        }

        &-text {
            font-weight: 700;
            font-size: 14px;
            color: var(--color-white);
        }
    }
}

// media

@media(max-width: 1350px) {
    .card {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &__wrapper {
            grid-template-columns: repeat(3, 1fr);
            gap: 30px;
        }
    }
}

@media(max-width: 1078px) {
    .card {

        &__wrapper {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media(max-width: 778px) {
    .card {

        &__wrapper {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}

@media(max-width: 520px) {
    .card {

        &__top {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 25px;
        }

        &__item {
            width: 280px;

            &-top {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                max-width: 240px;
                height: 180px;
            }
        }

        &__pic {
            width: 240px;
            height: 180px;
        }

        &__btn {

            &-buy {
                width: 182px;
                padding: 14px 27px; 
            }
        }
    }

    .price {

        &__discount {
            width: 115px;
        }
    }
}