.contacts {
  padding-top: 40px;
  padding-bottom: 80px;

  &__place {
    display: flex;
    min-height: 512px;
    margin-bottom: 30px;

    &-info {
      margin-right: 20px;
      padding: 30px;
      background-color: var(--color-light-blue);
      border-radius: 8px;
    }

    &-item {
      padding-bottom: 30px;

      &--border {
        border-bottom: 1px solid var(--color-drivider);
      }
    }

    &-address {
      display: block;
      margin-bottom: 10px;

      font-weight: 500;
      color: var(--color-main);

      &:hover {
        color: var(--color-blue);
      }
    }

    &-tel {
      font-weight: 600;
      font-size: 24px;
      color: var(--color-darkblue);

      &:hover {
        color: var(--color-blue);
        text-decoration: underline;
      }
    }

    &-email {
      display: flex;
      align-items: center;
      margin-bottom: 30px;
      padding-top: 30px;
      
      &-link {
        font-weight: 600;
        font-size: 24px;
        color: var(--color-darkblue);

        &:hover {
          color: var(--color-blue);
          text-decoration: underline;
        }
      }
    }

    &-link {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }
  }

  &__worktime {
    display: flex;

    &-icon {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }

    &-text {
      color: var(--color-gray);
      margin-bottom: 10px;
    }

    &-date {
      font-weight: 600;
      font-size: 20px;
      color: var(--color-main);
    }
  }

  &__form {
    display: flex;
    margin-bottom: 20px;

    &-left {
      margin-right: 20px;
    }

    &-input {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-label {
      color: var(--color-main);
      margin-bottom: 8px;
    }

    &-field {
      width: 310px;
      height: 48px;
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;

      &::placeholder {
        color: var(--color-gray);
      }
    }

    &-textarea {
      display: flex;
      flex-direction: column;

      &-field {
        width: 531px;
        height: 140px;
        padding: 16px 20px;

        border: 1px solid var(--color-card-border);
        border-radius: 5px;
        resize: none;

        &::placeholder {
          color: var(--color-gray);
        }
      }
    }
  }

  &__send {
    display: flex;
    align-items: center;

    &-submit {
      width: 310px;
      height: 48px;
      margin-right: 20px;
      padding: 13px 113px;

      font-weight: 700;
      color: var(--color-white);

      background-color: var(--color-blue);
      border-radius: 5px;
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-darkblue);
      }
    }

    &-confirm {
      display: flex;
    }

    &-checkbox {
      width: 20px;
      height: 20px;
      margin-right: 7px;
    }

    &-text {
      line-height: 130%;
      color: var(--color-main);
      max-width: 566px;
    }
  }
}

// media

@media(max-width: 1350px) {
  .contacts {

    &__map {
      height: 512px;
      
      img {
        height: 512px;
      }
    }
  }
}

@media(max-width: 998px) {
  .contacts {

    &__form {

      &-textarea {
        max-width: 354px;
        
        &-field {
          width: 354px;
        }
      }
    }

    &__place {

      &-email {

        &-link {
          font-size: 20px;
        }
      }

      &-tel {
        font-size: 20px;
      }
    }
  }
}

@media(max-width: 768px) {
  .contacts {
    padding-top: 30px;
    padding-bottom: 50px;

    &__place {
      flex-direction: column-reverse;

      &-info {
        margin-right: 0;
        margin-top: 20px;
      }

      &-email {

        &-link {
          font-size: 19px;
        }
      }
    }

    &__map {
      height: auto;

      img {
        height: auto;
      }
    }

    &__form {
      flex-direction: column;

      &-left {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }

    &__send {
      align-items: flex-start;
      flex-direction: column-reverse;

      &-submit {
        margin-right: 0;
      }

      &-confirm {
        margin-bottom: 20px;
      }
    }
  }
}

@media(max-width: 520px) {
  .contacts {

    &__place {

      &-info {
        padding: 15px 20px;
      }
    }

    &__form {

      &-field {
        width: 280px;
      }

      &-textarea {
        max-width: 280px;

        &-field {
          width: 280px;
        }
      }
    }

    &__send {

      &-text {
        max-width: 251px;
        font-size: 15px;
      }

      &-submit {
        width: 280px;
        padding: 14px 98px;
      }
    }
  }
}