.footer {
  padding-top: 40px;
  background-color: var(--color-light-blue);

  &__info {
    display: flex;
    flex-direction: column;
    margin-right: 98px;
  }

  &__top {
    display: flex;
    align-items: flex-start;
    padding-bottom: 40px;
  }

  &__logo {
    width: 184px;
    height: 60px;
    margin-bottom: 20px;
  }

  &__tel {
    font-weight: 600;
    font-size: 20px;
    color: var(--color-main);
    margin-bottom: 10px;

    &:hover {
      text-decoration: underline;
    }
  }

  &__worktime {
    color: var(--color-gray);
    margin-bottom: 20px;
  }

  &__address {
    display: flex;
    flex-direction: column;

    &-link {
      line-height: 130%;
      color: var(--color-main);
      padding-bottom: 10px;

      &:last-child {
        padding-bottom: 0;
      }

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__list {
    margin-right: 81px;

    &:last-child {
      margin-right: 0;
    }

    &--second {
      margin-right: 133px;
    }
  }
  
  &__item {
    margin-bottom: 10px;

    &:first-child {
      font-weight: 600;
      font-size: 20px;
      color: var(--color-gray);

      margin-bottom: 20px;
    }
  }

  &__link {
    line-height: 130%;
    color: var(--color-main);

    &:hover {
      color: var(--color-blue);
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid var(--color-drivider);
  }

  &__copy {
    color: var(--color-gray);
    margin-right: 10px;
  }

  &__social {
    display: flex;
    align-items: center;
    margin-right: 10px;

    &-link {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;

      &:hover {
        cursor: pointer;
      }
    }
  }

  &__text {
    font-size: 12px;
    line-height: 14px;
    color: var(--color-gray);
  }
}

// media

@media(max-width: 1078px) {
  .footer {

    &__top {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
      row-gap: 60px;
    }

    &__info {
      margin-right: 0;
    }

    &__list {
      margin-right: 0;
    }
  }
}

@media(max-width: 520px) {
  .footer {

    &__top {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 40px;
      padding-bottom: 30px;
    }

    &__bottom {
      flex-direction: column;
      align-items: flex-start;
    }

    &__copy {
      margin-right: 0;
      margin-bottom: 10px;
    }

    &__social {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
}