html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'Exo 2', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1330px;
}

.centered {
  text-align: center;
}

.logo {
  width: 184px;
  height: 60px;
}

.section-title {
  font-weight: 700;
  font-size: 36px;
  color: var(--color-main);

  margin-right: 30px;
}

.page-title {
  font-weight: 700;
  font-size: 36px;
  color: var(--color-main);

  margin-bottom: 30px;
}

.article-title {
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
  color: var(--color-main);

  margin-bottom: 20px;
}

.article-title-gray {
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
  color: var(--color-gray);

  margin-bottom: 20px;
}

.article-text {
  line-height: 120%;
  color: var(--color-main);

  max-width: 975px;
  padding-bottom: 10px;
  
  span {
    font-weight: 700;
  }

  &:last-child {
    padding-bottom: 0;
  }
}

.page-subtitle {
  font-weight: 700;
  font-size: 28px;
  line-height: 120%;
  color: var(--color-main);

  max-width: 975px;
}

.page-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px;

  &__link {
    display: flex;
    align-items: center;
    color: var(--color-main);

    &-active {
      color: var(--color-gray);
    }

    &:hover {
      text-decoration: underline;
    }
  }

  &__icon {
    padding: 0 15px;
  }
}

.page-pagination {
  display: flex;
  justify-content: center;

  &__btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 48px;
    height: 48px;
    margin-right: 10px;

    border: 1px solid var(--color-card-border);
    border-radius: 4px;
    transition: background-color .2s linear, color .2s linear;

    &-active {
      color: var(--color-white);
      background-color: var(--color-blue);
    }

    &-hidden {
      align-items: center;
      justify-content: flex-end;
      border: none;
    }

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      transition: background-color .2s linear, color .2s linear;
      color: var(--color-white);
      background-color: var(--color-blue);
    }
  }
}

.product-options {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 240px;
  padding: 14px 20px;

  border: 1px solid var(--color-card-border);
  border-radius: 4px;

  &__text {
    font-weight: 500;
    line-height: 130%;

    color: var(--color-main);
  }
}

// css for js

.click-cart {
  display: none;
}

.scroll-lock {
  overflow: hidden;
}

// media

@media(max-width: 1078px) {
  .section-title {
    font-size: 28px;
  }
}

@media(max-width: 998px) {
  .page-title {
    font-size: 28px;
  }
}

@media(max-width: 768px) {
  .page-subtitle {
    font-size: 24px;
  }

  .article-title-gray {
    font-size: 20px;
  }

  .article-title {
    font-size: 20px;
  }

  .product-options {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .page-nav {

    &__link {
      margin-bottom: 7px;
    }
  }
}

@media(max-width: 520px) {
  .section-title {
    font-size: 24px;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .page-title {
    font-size: 24px;
  }

  // .page-nav {

  //   &__link {
  //     margin-bottom: 7px;
  //   }
  // }

  .page-subtitle {
    font-size: 20px;
  }

  .page-pagination {

    &__btn {
      width: 40px;
      height: 40px;

      &--media {
        display: none;
      }
    }
  }
}