.vacancies {
  padding-top: 40px;
  padding-bottom: 80px;

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__info {
    margin-right: 20px;
  }

  &__list {
    max-width: 750px;

    background-color: var(--color-light-blue);
    border-radius: 8px;
  }

  &__item {

    &:hover {
      cursor: pointer;
    }
  }

  &__open {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 30px;
    padding: 14px 0;

    border-bottom: 1px solid var(--color-drivider);

    &:hover &-title {
      color: var(--color-darkblue);
    }

    &-title {
      color: var(--color-main);
      font-size: 16px;
      font-weight: 500;
    }

    &--last {
      border: none;
    }
  }

  &__inner {
    padding: 20px 30px 30px 30px;

    background-color: var(--color-white);
    border: 1px solid var(--color-card-border);

    &-list {
      max-width: 672px;
      margin-left: 15px;
      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-item {
      line-height: 130%;
      color: var(--color-main);

      padding-bottom: 10px;
      list-style-type: disc;

      &:last-child {
        padding-bottom: 0;
      }
    }
  }

  &__join {
    padding: 30px;

    border: 1px solid var(--color-card-border);
    border-radius: 8px;
  }

  &__form {
    padding-bottom: 20px;
    border-bottom: 1px solid var(--color-drivider);

    &-field {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;
    }

    &-label {
      margin-bottom: 8px;
    }

    &-input {
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 5px;
      transition: border .2s ease-in-out;

      &::placeholder {
        color: var(--color-gray);
      }

      &:focus {
        transition: border .2s ease-in-out;
        border: 1px solid var(--color-blue);
      }
    }

    &-info {
      font-size: 13px;
      line-height: 100%;
      color: var(--color-gray);

      margin-top: 10px;
    }

    &-textarea {
      min-height: 114px;
      padding: 16px 20px;

      border: 1px solid var(--color-card-border);
      border-radius: 8px;
      transition: border .2s ease-in-out;
      resize: none;

      &::placeholder {
        color: var(--color-gray);
      }

      &:focus {
        transition: border .2s ease-in-out;
        border: 1px solid var(--color-blue);
      }
    }
  }

  &__submit {

    &-btn {
      width: 100%;
      margin-bottom: 10px;
      padding: 13px 113px;

      font-weight: 700;
      color: var(--color-white);

      background-color: var(--color-blue);
      border-radius: 5px;
      transition: background-color .2s linear;

      &:hover {
        transition: background-color .2s linear;
        background-color: var(--color-darkblue);
      }
    }
  }

  &__checkbox {
    display: flex;

    &-input {
      width: 20px;
      height: 20px;
      margin-right: 7px;
    }

    &-confirm {
      max-width: 281px;
      font-size: 13px;
      line-height: 130%;
      color: var(--color-main);
    }
  }

  &__contact {
    margin-top: 20px;

    &-title {
      margin-bottom: 10px;
    }

    &-phone {
      font-weight: 700;
      font-size: 28px;
      line-height: 120%;
      color: var(--color-blue);

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

// media

@media(max-width: 998px) {
  .vacancies {

    &__content {
      flex-direction: column;
    }

    &__info {
      margin-right: 0;
      margin-bottom: 40px;
    }

    &__list {
      max-width: 956px;
    }

    &__checkbox {

      &-confirm {
        max-width: 500px;
      }
    }
  }
}

@media(max-width: 768px) {
  .vacancies {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}

@media(max-width: 520px) {
  .vacancies {

    &__open {
      margin: 0 20px;
    }

    &__inner {
      padding: 20px;
    }

    &__join {
      padding: 20px;
    }

    &__checkbox {

      &-confirm {
        max-width: 220px;
      }
    }
  }
}

@media(max-width: 375px) {
  .vacancies {

    &__submit {

      &-btn {
        padding: 13px 80px;
      }
    }
  }
}