.stocks-page {
  padding-top: 40px;
  padding-bottom: 80px;

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 30px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 50px;
    padding-right: 50px;

    background-color: var(--color-light-blue);
    border-radius: 8px;
    transition: background-color .2s linear;

    &--nonepadding {
      padding-right: 0;
    }

    &:first-child {
      padding-right: 0;
    }

    &:hover {
      transition: background-color .2s linear;
      background-color: #b0cfd0;
      cursor: pointer;
    }
  }
}

// media

@media(max-width: 1200px) {
  .stocks-page {

    &__grid {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

@media(max-width: 768px) {
  .stocks-page {
    padding-top: 30px;
    padding-bottom: 50px;

    &__item {
      padding-left: 20px;
      padding-right: 0;
      height: 130px;

      &-img {
        max-width: 115px;
      }
    }
  }
}

@media(max-width: 520px) {
  .stocks-page {

    &__item {
      padding-left: 10px;

      &-title {
        font-size: 16px;
      }
    }
  }
}