.warranty {
  padding-top: 40px;
  padding-bottom: 80px;

  &__title {
    margin-bottom: 40px;
  }

  &-article {

    &-title {

      &--upper {
        text-transform: uppercase;
      }
    }
  }

  &__specif {
    margin: 40px 0;

    &-text {
      padding-bottom: 20px;
    }
  }

  &__table {
    max-width: 975px;
    margin-bottom: 20px;

    th {
      font-weight: 700;
      line-height: 25px;
      text-align: left;
      color: var(--color-main);
      padding: 7px;

      border: 1px solid var(--color-light-blue);
    }

    td {
      line-height: 25px;
      color: var(--color-gray);
      text-align: left;
      padding: 7px;

      border: 1px solid var(--color-light-blue);
    }
  }

  &__rules {
    margin-bottom: 40px;

    &-title {
      display: inline-block;
      font-weight: 700;
      line-height: 21px;
      color: var(--color-main);

      margin-bottom: 10px;
    }
  }

  &__list {
    margin-left: 25px;
    margin-bottom: 10px;

    &-item {
      list-style-type: disc;
      margin-bottom: 8px;

      &--deceminal {
        list-style-type: decimal;
      }
    }

    &-nested {
      margin-top: 8px;
    }
  }

  &__prohibited {
    margin-bottom: 40px;
  }

  &__service {
    margin-bottom: 60px;
  }

  &__letter {
    margin-bottom: 20px;
    
    &-title {
      margin-bottom: 20px;
    }

    &-info {
      display: inline-block;
      margin-bottom: 20px;
      padding-right: 18px;
      background-color: yellow;

      line-height: 130%;
      color: var(--color-main);
    }
  }

  &__info {

    &-text {
      font-weight: 700;
      font-size: 28px;
      line-height: 120%;
      color: var(--color-main);

      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

// media

@media(max-width: 1024px) {
  .warranty {

    &__specif {
      overflow-x: auto;
    }
  }
}

@media(max-width: 768px) {
  .warranty {
    padding-top: 30px;
    padding-bottom: 50px;

    &__info {

      &-text {
        font-size: 24px;
      }
    }

    &__table {

      th {
        padding: 4px;
        font-size: 15px;
      }

      td {
        padding: 4px;
        font-size: 14px;
      }
    }
  }
}

@media(max-width: 520px) {
  .warranty {

    &__info {

      &-text {
        font-size: 20px;
      }
    }
  }
}