.modal {
  display: none;

  &__overlay {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba($color: #2A5275, $alpha: 0.6);

    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
  }

  &__window {
    max-width: 370px;
    
    background-color: var(--color-white);
    border-radius: 8px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 10px 30px;

    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    background-color: var(--color-light-blue);
  }

  &__title {
    margin-bottom: 0;
  }

  &__close {
    width: 48px;
    height: 48px;

    background-image: url('../img/modal-close.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__content {
    padding: 0 30px 30px 30px;
  }

  &__form {
    margin-bottom: 10px;

    &-field {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-label {
      display: inline-block;
      color: var(--color-main);

      margin-bottom: 8px;
    }

    &-input {
      width: 310px;
      height: 48px;
      padding: 16px 20px;

      border: 1px solid #EAEAF0;
      border-radius: 5px;
    }
  }

  &__text {
    max-width: 310px;
    margin-bottom: 10px;
    color: var(--color-main);
  }

  &__recover {
    display: block;
    margin-bottom: 20px;
  }

  &__remember {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    &-input {
      width: 20px;
      height: 20px;
      margin-right: 7px;
    }

    &-label {
      color: var(--color-main);
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__button {
    max-width: 310px;
    margin-bottom: 20px;
    padding: 16px 133px;

    color: var(--color-white);

    background-color: var(--color-darkblue);
    border-radius: 4px;
    transition: background-color .2s linear;

    &:hover {
      transition: background-color .2s linear;
      background-color: var(--color-blue);
    }
  }

  &__link {
    color: var(--color-blue);

    &:hover {
      color: var(--color-darkblue);
    }
  }

}

// media

@media (max-width: 375px) {
  .modal {

    &__overlay {
      align-items: flex-end;
    }

    &__window {
      max-width: 340px;
    }

    &__form {

      &-input {
        width: 280px;
      }
    }

    &__button {
      width: 280px;
      padding: 16px 60px;
    }
  }
}

.modal.active {
  display: block;
}

.modal.close {
  display: none;
}