.lesale {
  padding-top: 40px;
  padding-bottom: 80px;

  &__intro {
    margin-bottom: 40px;

    &-text {
      line-height: 130%;
    }

    &-list {
      margin-left: 20px;
      margin-bottom: 20px;
    }

    &-item {
      list-style-type: disc;
      padding-bottom: 8px;

      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}

// media

@media(max-width: 768px) {
  .lesale {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}