.product-card {
  padding-top: 40px;
  padding-bottom: 40px;

  &__content {
    display: flex;
  }

  &__swiper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 530px;
    height: 460px;
    margin-left: 0;
    margin-right: 20px;

    &-nav {
      display: flex;
      align-items: center;
      margin-top: -10px;
    }

    &-prev {
      margin-right: 248px;
      background-image: url('../img/arrow-left.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: 38px 11px;
    }

    &-next {
      background-image: url('../img/arrow-right-dark.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: 38px 11px;
    }
  }

  &__slide {
    max-width: 530px;

    background-image: url('../img/product-card-swiper.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__btn {
    width: 228px;
    padding: 14px 50px;

    font-weight: 700;
    color: var(--color-white);

    border: 1px solid var(--color-blue);
    border-radius: 4px;
  }

  &__main {
    margin-bottom: 30px;
    padding: 30px;

    border: 1px solid var(--color-card-border);
    border-radius: 10px;

    &-top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 11px;
    }

    &-buy {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-btn {
      display: flex;
      align-items: center;

      &-buy {
        color: var(--color-blue);
        transition: background-color .2s linear, color .2s ease-in-out;

        &:hover {
          transition: background-color .2s linear, color .2s ease-in-out;
          color: var(--color-white);
          background-color: var(--color-blue);
        }
      }
    }

    &-cart {
      margin-right: 10px;

      background-color: var(--color-blue);
      transition: background-color .2s linear;

      &:hover {
        background-color: var(--color-darkblue);
        transition: background-color .2s linear;
      }
    }
  }

  &__price {
    margin-right: 60px;

    &-discount {
      display: flex;
      align-items: center;
    }

    &-old {
      margin-right: 8px;
    }

    &-new {

      &-text {
        font-size: 32px;
      }
    }
  }

  &__info {
    max-width: 750px;

    &-bottom {
      display: flex;
    }
  }

  &__shipping {
    margin-right: 20px;
  }

  &__function {
    display: flex;

    &-icon {
      width: 24px;
      height: 24px;
      margin-right: 12px;
    }

    &-title {
      padding-top: 4px;
      font-weight: 700;
      color: var(--color-main);

      margin-bottom: 10px;
    }

    &-descr {
      line-height: 120%;
      max-width: 331px;
      color: var(--color-main);
    }
  }
}

// media

@media(max-width: 1350px) {
  .product-card {

    &__content {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }

    &__swiper {
      grid-column: 2 / 1 span;
      margin-right: 0;
      margin-bottom: 30px;
    }
    
    &__info {
      max-width: 1300px;
      grid-column: 3 span;

      &-bottom {
        justify-content: space-between;
      }
    }

  }
}

@media(max-width: 768px) {
  .product-card {
    padding-top: 30px;
    padding-bottom: 50px;

    &__swiper {
      width: 310px;
      height: 260px;
    }

    &__main {

      &-top {
        flex-direction: column;
      }

      &-buy {
        flex-direction: column;
        align-items: flex-start;
      }

      &-btn {
        width: 100%;
        justify-content: space-between;
      }
    }

    &__price {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
}

@media(max-width: 568px) {
  .product-card {

    &__main {

      &-btn {
        flex-direction: column;
        align-items: flex-start;
      }

      &-cart {
        margin-right: 0;
      }
    }

    &__info {

      &-bottom {
        flex-direction: column;
      }
    }

    &__shipping {
      margin-right: 0;
      margin-bottom: 20px;
    }

    &__btn {
      margin-bottom: 10px;

      &-buy {
        width: 228px;
        padding: 14px 50px;
        margin-right: 0;
      }
    }
  }
}

@media(max-width: 375px) {
  .product-card {

    &__info {

      &-title {
        max-width: 280px;
      }
    }

    &__main {
      border: none;
      padding: 0;
    }

    &__function {

      &-descr {
        max-width: 246px;
      }
    }
  }
}