.news-section-2 {
  padding-top: 40px;
  padding-bottom: 90px;

  &__content {
    display: flex;
    align-items: flex-start;

    &-descr {
      margin-right: 20px;
    }

    &-text {
      line-height: 21px;
      color: var(--color-main);

      max-width: 750px;
      margin-bottom: 18px;

      &:last-child {
        margin-bottom: 0;
      }

      &--top {
        margin-bottom: 40px;
      }
    }

    &-title {
      font-weight: 600;
      font-size: 24px;
      color: var(--color-main);

      margin-bottom: 20px;
    }

    &-picture {

      &--media {
        display: none;
      }
    }
  }
}

// media

@media(max-width: 1200px) {
  .news-section-2 {

    &__content {
      flex-direction: column;
      
      &-descr {
        margin-right: 0;
        margin-bottom: 30px;
      }

      &-picture {
        display: none;

        &--media {
          display: block;
        }
      }
    }
  }
}

@media(max-width: 768px) {
  .news-section-2 {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}

@media(max-width: 520px) {
  .news-section-2 {

    &__content {

      &-title {
        font-size: 20px;
      }
    }
  }
}