.hero {
    padding-top: 20px;
    padding-bottom: 80px;

    &__img {
        margin-left: 324px;
    }
}

// media

@media(max-width: 1350px) {
    .hero {

        &__img {
            width: 100%;
            margin-left: 0;
        }
    }
}

@media(max-width: 778px) {
    .hero {
        padding-bottom: 50px;
    }
}