.stocks {
  padding-bottom: 80px;
  
  &__text {
    font-weight: 700;
    font-size: 28px;
    line-height: 120%;
    color: var(--color-main);

    &-segway {
      max-width: 234px;
    }

    &-smartwatch {
      max-width: 188px;
    }

    &-percent {
      max-width: 234px;
    }
    
    &-balance {
      max-width: 333px;
    }
  }
}

.info {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__left {
    margin-right: 10px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 640px;
    height: 180px;
    margin-right: 20px;
    padding: 3px 30px 3px 50px;

    background-color: var(--color-light-blue);
    border-radius: 8px;

    &:last-child {
      margin-right: 0;
    }
  }
}

// media

@media(max-width: 1078px) {
  .stocks {

    &__text {

      &-percent {
        font-size: 24px;
      }

      &-smartbalance {
        font-size: 24px;
      }
    }
  }
}

@media(max-width: 998px) {
  .info {
    flex-direction: column;

    &__item {
      margin-right: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

@media(max-width: 778px) {
  .stocks {
    padding-bottom: 60px;

    &__text {
      font-size: 22px;
    }
  }

  .info {

    &__item {
      width: 480px;
    }
  }
}

@media(max-width: 520px) {
  .stocks {
    padding-bottom: 50px;

    &__text {
      font-size: 16px;
    }

    &__img {
      width: 131px;
    }

    &__percent {
      width: 96px;
      height: 83px;
    }

    &__right {

      &-img {
        width: 111px;
      }
    }
  }

  .info {

    &__item {
      width: 340px;
      height: 160px;
      padding-right: 10px;
      padding-left: 20px;
    }
  }
}

@media(max-width: 450px) {
  .info {

    &__item {
      width: 280px;
    }
  }
}