.viewed {
  padding-top: 40px;
  padding-bottom: 80px;

  &__product-options {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    margin-bottom: 20px;
  }
}

// media

@media(max-width: 768px) {
  .viewed {
    padding-top: 30px;
    padding-bottom: 50px;

    &__product-options {
      flex-direction: column;
      align-items: flex-start;
    }
  }

}