.header {
    
    &__container {
        display: flex;
        align-items: center;
        padding: 10px 15px;
    }

    &__logo {
        margin-right: 126px;
    }

    &__info {
        margin-left: 20px;
        margin-right: 58px;

        &-tel {
            font-weight: 500;
            color: var(--color-main);

            margin-right: 23px;

            &:last-child {
                margin-right: 20px;
            }

            &:hover {
                text-decoration: underline;
            }
        }

        &-worktime {
            font-weight: 500;
            color: var(--color-gray);
        }
    }

    &__menu {
        display: flex;
        align-items: center;
    }
    
    &__search {
        display: flex;
        align-items: center;

        position: relative;
        width: 107px;
        height: 36px;
        margin-right: 8px;
        padding: 0 12px;
        border-bottom: 1px solid var(--color-drivider);

        &-input {
            width: 24px;
            height: 24px;

            border: none;
            background-color: transparent;
            padding: 0;

            background-image: url('../img/search-icon.png');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    &__input {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        
        border: none;
        background-color: transparent;
        padding: 0;
    }

    &__icons {
        display: flex;
        align-items: center;
        margin-right: 20px;

        &-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 48px;
            height: 48px;

            &:hover {
                border: 1px solid var(--color-drivider);
            }
        }
    }

    &__sigin {
        font-weight: 500;
        color: var(--color-white);

        padding: 12px 26px;
        background-color: var(--color-darkblue);
        border-radius: 4px;

        &:hover {
            background-color: #326089;
        }
    }

    // bottom

    &__bottom {
        background-color: var(--color-menublue);

        &-container {
            display: flex;
            align-items: center;
        }
    }

    &__nav {
        position: relative;
        width: 310px;

        &-link {
            display: flex;
            align-items: center;
            font-weight: 500;
            color: var(--color-white);

            padding: 14px 20px;

            &:hover {
                background-color: #243c53;
            }
        }

        &-btn {
            width: 32px;
            height: 32px;
            margin-right: 10px;

            background-image: url('../img/menu-icon.png');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        margin-left: 20px;
    }

    &__item {
        margin-right: 40px;

        &:last-child {
            margin-right: 0;
        }

        &-list {
            position: relative;
            height: 60px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    &__link {
        font-weight: 500;
        color: var(--color-white);

        &:hover {
            color: var(--color-gray);
        }
    }
}

.mobile__nav {
    width: 100%;
    display: none;

    position: absolute;
    left: 0;
    top: 60px;
    z-index: 10000;
    padding: 8px 0;

    background-color: var(--color-light-blue);
    border-radius: 0px 0px 8px 8px;

    &-link {
        display: flex;
        align-items: center;
        padding: 8px 16px;

        &:hover {
            background-color: #dae4ed;
        }
    }

    &-icon {
        width: 32px;
        height: 32px;
        margin-right: 10px;
    }

    &-text {
        display: block;

        font-weight: 500;
        color: var(--color-main);
    }
}

.header__nav:hover .mobile__nav {
    display: block;
}

.item__list {
    position: absolute;
    left: 0;
    top: 60px;
    z-index: 1000;

    display: none;
    width: 100%;
    padding: 8px 0;

    background-color: var(--color-light-blue);
    border-radius: 0px 0px 8px 8px;

    &-menu {
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &-link {
        font-weight: 500;
        color: var(--color-main);

        &:hover {
            color: var(--color-blue);
        }
    }
}

.header__item-list:hover .item__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

// media

@media(max-width: 1350px) {
    .header {

        &__container {
            justify-content: space-between;
        }

        &__logo {
            margin-right: 0;
        }

        &__menu {
            margin-right: 0;
        }

        &__logo {
            max-width: 160px;
        }
    }
}

@media(max-width: 1200px) {
    .header {

        &__info {
            display: none;
        }

        &__bottom {

            &-container {
                justify-content: space-between;
            }
        }

        &__nav {
            width: 220px;
        }
    }
}

@media(max-width: 998px) {
    .header {

        &__bottom {
            display: none;
        }
    }
}

@media(max-width: 768px) {
    .header {
        box-shadow: 0px 4px 6px rgba(42, 82, 117, 0.06);

        &__search {
            display: none;
        }

        &__icons {

            &-item {

                &:last-child {
                    display: none;
                }
            }
        }

        &__sigin {
            width: 80px;
            height: 40px;
            padding: 13px 20px;
            
            font-size: 14px;
        }
    }

    .logo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        max-width: 120px;
    }
}

@media(max-width: 450px) {
    .header {

        &__icons {
            display: none;
        }
    }
}