.news-info {
  padding-bottom: 80px;

  &__content {
    display: flex;
    align-items: center;
  }

  &__item {
    max-width: 640px;
    height: 210px;
    margin-right: 20px;
    padding: 30px 50px;

    background-color: var(--color-light-blue);
    border-radius: 8px;

    &-title {
      font-weight: 700;
      font-size: 28px;
      color: var(--color-main);

      margin-bottom: 20px;

      &--blue {
        color: var(--color-darkblue);
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__descr {
    line-height: 120%;
    color: var(--color-main);

    max-width: 540px;
    margin-bottom: 20px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__time {
    font-weight: 500;
    color: var(--color-gray);
  }
}

// media

@media(max-width: 1020px) {
  .news-info {

    &__content {
      flex-direction: column;
    }

    &__item {
      margin-right: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

@media(max-width: 768px) {
  .news-info {
    padding-bottom: 50px;

    &__item {

      &-title {
        font-size: 18px;
      }
    }
  }
}

@media(max-width: 520px) {
  .news-info {

    &__item {
      padding: 20px;
    }
  }
}

@media(max-width: 375px) {
  .news-info {

    &__item {
      min-height: 240px;
    }

    &__descr {
      max-width: 250px;
    }
  }
}