.drop {
  padding-top: 40px;
  padding-bottom: 80px;

  &__intro {
    margin-bottom: 40px;

    &-text {
      line-height: 130%;
    }

    &-descr {
      margin-bottom: 20px;
    }

    &-list {
      margin-bottom: 40px;
      margin-left: 15px;
    }

    &-item {
      line-height: 130%;
      color: var(--color-main);

      padding-bottom: 15px;
      list-style-type: decimal;

      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}

// media

@media(max-width: 768px) {
  .drop {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}